<template>
  <div>
    <h1>网站索引</h1>
    <a-divider />
    <a-row>
      <a-col :span="10" v-for="item in urlList" :key="item.name">
        <a-card hoverable @click="jumpTo(item.url)" style="width: 300px;margin: 10px;" :body-style="{ padding: 0 }">
          <div>
            <div style="padding: 14px;">
              <a :href="item.url" style="color: #2C405A;" target="_blank">{{item.name}}</a>
            </div>
          </div>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: 'index',
  methods: {
    jumpTo (e) {
      window.location.href = e
    }
  },

  data () {
    return {
      urlList: [
        {
          name: '官网',
          url: 'https://www.hustunique.com',
          online: true
        },
        {
          name: 'BBS',
          url: 'https://bbs.hustunique.com',
          online: true
        },
        {
          name: 'HR 系统',
          url: 'https://hr.hustunique.com',
          online: true
        },
        {
          name: '短链接服务',
          url: 'https://uniq.st',
          online: true
        },
        {
          name: 'Family Tree',
          url: 'https://fmt.hustunique.com/',
          online: true
        },
        {
          name: 'Unique Hackday',
          url: 'https://hack.hustunique.com/',
          online: true
        },
        {
          name: '公众号',
          url: 'https://mp.weixin.qq.com/',
          online: true
        },
        {
          name: '企业微信',
          url: 'https://work.weixin.qq.com/',
          online: true
        }
      ]
    }
  }
}
</script>

<style>
